<template>
  <v-select
    :error-messages="errors"
    :items="resources.data"
    item-text="name"
    item-value="_id"
    label="Role"
    name="role"
    outlined
    v-model="dataset"
  ></v-select>
</template>

<script>
import Resources from '@/core/models/Resources'
import Api from '@/modules/Role/routes/api'

export default {
  props: ['value', 'errors'],

  computed: {
    dataset: {
      get () { return this.value },
      set ( val ) { this.$emit( 'input', val ) }
    }
  },

  data: () => ({
    resources: new Resources
  }),

  mounted () {
    this.resources.getResources({
      method: 'GET',
      url: Api.main(),
      params: {
        per_page: -1
      }
    })
  }
}
</script>