<template>
  <img :src="logo" :height="height" :width="width" alt="App Logo" />
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  props: [ 'height', 'width', 'src' ],

  computed: {
    ...mapGetters({
      setting: 'setting/setting',
    }),

    logo () {
      return this.setting[ 'app:logo' ] ? this.setting[ 'app:logo' ] : window.location.origin + '/favicon.ico'
    }
  },
}
</script>