import Api from '../routes/api'

const state = () => ({
  setting: {},
})

const getters = {
  setting: state => state.setting,
}

const mutations = {
  'SET' ( state, payload ) {
      state.setting = payload
  },
}

const actions = {
  set: async ( { commit }, payload ) => {
    commit( 'SET', payload )
  },

  init: async ( { commit } ) => {
    try {
      const response = await window.axios( Api.getAll() )
      commit( 'SET', response.data )
    } catch( err ) {
      // console.error( err.response ? err.response.data.errors : err )
    }
  },
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}
