<template>
  <v-toolbar
    elevation="0"
    class="mb-5"
  >
    <v-btn
      :to="from"
      exact
      icon
      v-if="hasFrom"
    >
      <v-icon>mdi-arrow-left</v-icon>
    </v-btn>
    <v-toolbar-title>
      <slot name="title" ></slot>
    </v-toolbar-title>
  </v-toolbar>
</template>

<script>
export default {
  props: [ 'to' ],
  computed: {
    hasFrom () {
      return this.from ? true : false
    },

    from () {
      let from = this.$route.query.from
      if ( !from && this.$route.name !== 'dashboard' )
        from = { name: 'dashboard' } 
      if ( this.to )
        from = this.to
      return from
    }
  },
}
</script>