<template>
  <div></div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  props: [ 'title', 'description', 'keywords', 'logo' ],

  computed: {
    ...mapGetters({ 
      setting: 'setting/setting',
    }),
  },

  methods: {
    ...mapActions({
      initSetting: 'setting/init',
    }),
  },

  mounted () {
    if( this.title && this.title === this.setting['app:name'] || !this.title )
      document.title = this.setting['app:name'] || ''
    else if ( this.title )
      document.title = `${ this.title } | ${ this.setting['app:name'] }`

    if( this.setting['app:logo'] )
      document.querySelector('link[name=icon]').href = this.setting['app:logo']


    document.querySelector('meta[name=description]').content = this.description || this.setting['app:description'] || ''
    document.querySelector('meta[name=keywords]').content = this.keywords || this.setting['app:keywords'] || ''
  }
}
</script>