const state = () => ({
  sidebar: {
    model: true
  },
})

const getters = {
  sidebar: state => state.sidebar,
}

const mutations = {
  'TOGGLE' ( state, payload ) {
    state.sidebar.model = payload
  },

  'SHOW' (state) {
    state.sidebar.model = true
  },

  'HIDE' (state) {
    state.sidebar.model = false
  },
}

const actions = {
  toggle: ({commit}, payload) => {
    commit('TOGGLE', payload)
  },

  show: ({commit}) => {
    commit('SHOW')
  },

  hide: ({commit}) => {
    commit('HIDE')
  },
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}
