<template>
  <div>
    <html-head 
      v-if="setting['app:name']"
    ></html-head>
    <router-view />
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters({
      setting: 'setting/setting',
    }),
  },

  created () {
    this.initSetting()
  },

  methods: {
    ...mapActions({
      initSetting: 'setting/init',
    }),
  },
}
</script>