<template>
  <div v-text="name" ref="wavy-text" class="ed-wavy-text"></div>
</template>

<script>
export default {
  props: ['name'],

  mounted () {
    const wavyText = this.$refs['wavy-text']
    const wavyChars = wavyText.textContent.split('')
    wavyText.innerHTML = ''
    wavyChars.forEach((char,i) => {
      wavyText.innerHTML += `<span style="--i:${i+1}">${char}</span>`
    })
  }
}
</script>