<template>
  <div class="d-inline">
    <slot v-if="allowed"></slot>
  </div>
</template>

<script>
export default {
  props: [ 'code' ],

  computed: {
    allowed () {
      const user = JSON.parse( localStorage.getItem( 'user' ) )
      const permissions = user.roleDetails.permissions
      return permissions.find( permission => permission.code === this.code )
    }
  },
}
</script>