<template>
  <div class="ed-zoom-and-move" ref="parent">
    <div class="content">
      <div class="image">
        <slot></slot>
      </div>
    </div>
    <div class="details  ed-width-100">
      <slot name="details"></slot>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    hovering: false
  }),

  mounted() {
    const parent = this.$refs.parent
    parent.addEventListener('mouseover', () => {
      this.hovering = true
      parent.querySelector('.content').style.transform = `scale(1.3)`
    })

    parent.addEventListener('mousemove', (e) => {
      if(this.hovering) {
        const contentXCenter = parent.clientWidth / 2
        const contentYCenter = parent.clientHeight / 2
        const xAxis = ((e.x - parent.getBoundingClientRect().left - contentXCenter) / contentXCenter) * 10
        const yAxis = ((e.y - parent.getBoundingClientRect().top -  contentYCenter) / contentYCenter) * 10
        parent.querySelector('.content .image').style.transform = `scale(1.3) translate(${xAxis}%,${yAxis}%)`
      }
    })

    parent.addEventListener('mouseout', () => {
      this.hovering = true
      parent.querySelector('.content .image').style.transform = ``
      parent.querySelector('.content').style.transform = `scale(1)`
    })
  }
}
</script>