import Auth from '@/core/models/Auth'

const state = () => ({
  auth: new Auth,
})

const getters = {
  auth: state => state.auth,
}

const mutations = {
  'SET' ( state, payload ) {
    if( payload.token )
      state.auth.setToken( payload.token )
    if( payload.user )
      state.auth.setUser( payload.user )
  },
}

const actions = {
  set: ({commit}, payload) => {
    commit('SET', payload)
  },
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}
