<template>
  <v-card elevation="0">
    <v-card-title>
      <v-row>
        <v-col cols="4">
          Variation Option
        </v-col>
        <v-col cols="8" class="text-right">
          <allowed :code="`categories.delete`">
            <v-btn 
              text
              @click="deleteAll"
            >
              <span v-text="`Delete`"></span>
            </v-btn>
          </allowed>
        </v-col>
      </v-row>
    </v-card-title>
    <v-card-text>
      <template v-if="resources.data.length === 0">
        No variation yet
      </template>
      <v-treeview
        :items="resources.data"
        v-model="selected"
        item-key="_id"
        multiple-active
        selectable
        hoverable
        disabled
        dense
      >
        <template v-slot:append="{ item }">
          <option-buttons
            :api="api"
            :deletePermanently="true"
            :edit="true"
            :item="item"
            name="variation"
            @changed="changed"
            :modifyOnClickEdit="true"
            @onClickEdit="onClickEdit"
          >
          </option-buttons>  
        </template>
      </v-treeview>
    </v-card-text>
  </v-card>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  props: [ 'value', 'api', 'changed', 'resources' ],

  computed: {
    dataset: {
      get () {
        return this.value
      },
      set ( val ) {
        this.$emit( 'input', val )
      }
    }
  },

  data: () => ({
    selected: []
  }),

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
      setDialog: 'dialog/set',
      hideDialog: 'dialog/hide',
    }),

    onClickEdit ( value ) {
      this.$emit( 'onClickEdit', value )
    },

    isNoSelectedItems () {
      if( this.selected.length === 0) {
        this.setDialog({
          model: true,
          title: 'No selected items',
          text: `Please select items to proceed.`
        })

        return true
      }
      
      return false
    },

    async deleteAll () {
      if( this.isNoSelectedItems() ) return
      let data = null
      this.setDialog({
        model: true,
        title: 'Delete an item',
        text: `Are you sure to delete ${ this.selected.length} selected items permanently?`,
        confirm: true,
        proceed: async () => {
          this.setDialog({ loading: true })
          for( const item of this.selected ) {
            data = await this.resources.request({
              method: 'delete',
              url: this.api.alone( item )
            })
          }

          if( !data ) return

          this.hideDialog()
          this.setSnackbar({
            model: true,
            text: `Selected items deleted`,
            timeout: 3000
          })

          this.changed()
        }
      })
    },
  },
}
</script>