<template>
  <v-card elevation="0">
    <p>Category</p>
    <v-treeview
      :error-messages="errors"
      :items="resources.data"
      v-model="dataset"
      item-key="_id"
      multiple-active
      selectable
      disabled
      dense
    >
    </v-treeview>
    <div class="v-text-field__details pl-3">
      <div class="v-messages error--text" role="alert">
        <div class="v-messages__wrapper">
          <div class="v-messages__message message-transition-enter-to" v-for="(error,i) in errors" :key="i" v-text="error">"description" is required</div>
        </div>
      </div>
    </div>
    <input v-for="(item, i) in dataset" :key="i" type="hidden" name="categories[]" :value="item">
  </v-card>
</template>

<script>
import Resources from '@/core/models/Resources'

export default {
  props: [ 'requestOptions', 'value', 'errors' ],

  computed: {
    dataset: {
      get () {
        return this.value
      },
      set ( val ) {
        this.$emit( 'input', val )
      }
    }
  },

  data: () =>({
    resources: new Resources
  }),

  methods: {

  },

  mounted () {
    this.resources.getResources( this.requestOptions )
  }
}
</script>