<template>
  <span v-text="name"></span>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters({
      setting: 'setting/setting',
    }),

    name () {
      return this.setting[ 'app:name' ] ? this.setting[ 'app:name' ] : 'Please add app name'
    }
  },
}
</script>