<template>
  <v-app app>
    <v-app-bar elevation="0" app color="white">
      <v-app-bar-nav-icon @click.stop="sidebarmodel = !sidebarmodel"></v-app-bar-nav-icon>
      <v-toolbar-title class="d-flex align-center"></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-menu
        bottom
        left
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            text
            v-bind="attrs"
            v-on="on"
          >
            {{ auth.user.firstName + " " + auth.user.lastName }}
          </v-btn>
        </template>

        <v-list>
          <v-list-item-group v-model="option">
            <v-list-item>
              <v-list-item-title @click="logout">Logout</v-list-item-title>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </v-menu>
    </v-app-bar>

    <sidebar :sidebar="sidebarmodel"></sidebar>
    
    <!-- Sizes your content based upon application components -->
    <v-main app>
      <snack-bar></snack-bar>
      <default-dialog></default-dialog>
      <!-- Provides the application the proper gutter -->
      <v-container fluid class="ed-height-100">
        <slot>
          <!-- If using vue-router -->
          <!-- <router-view></router-view> -->
        </slot>
      </v-container>
    </v-main>

    <!-- <v-footer app>
      
    </v-footer> -->
  </v-app>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import DefaultDialog from '../Dialog/DefaultDialog.vue'

export default {
  components: { DefaultDialog },
  computed: {
    ...mapGetters({
      sidebar: 'sidebar/sidebar',
      auth: 'auth/auth',
    }),

    sidebarmodel: {
      set ( value ) {
        this.toggle( value )
      },
      get () {
        return this.sidebar.model
      },
    }
  },

  data: () => ({
    // auth: new Auth,
    option: null,
  }),

  methods: {
    ...mapActions({
      toggle: 'sidebar/toggle',
    }),

    logout () {
      this.auth.logout()
      this.$router.push({ name: 'public' })
    }
  },

  mounted () {
    console.log( this.auth )
  }
}
</script>
