<template>
  <div>
    <allowed :code="`${ pluralName }.read`">
      <v-tooltip bottom v-if="view">
        <template v-slot:activator="{ on, attrs }">
          <v-btn 
            icon
            v-bind="attrs"
            v-on="on"
          >
            <v-icon @click="clickedView">mdi-eye-circle-outline</v-icon>
          </v-btn>
        </template>
        <span>View {{ formattedName }}</span>
      </v-tooltip>
    </allowed>
    <allowed :code="`${ pluralName }.update`">
      <v-tooltip bottom v-if="edit">
        <template v-slot:activator="{ on, attrs }">
          <v-btn 
            icon
            v-bind="attrs"
            v-on="on"
            @click="clickedEdit"
          >
            <v-icon>mdi-circle-edit-outline</v-icon>
          </v-btn>
        </template>
        <span>Edit {{ formattedName }}</span>
      </v-tooltip>
    </allowed>
    <allowed :code="`${ pluralName }.trash`">
      <v-tooltip bottom v-if="trash">
        <template v-slot:activator="{ on, attrs }">
          <v-btn 
            icon
            v-bind="attrs"
            v-on="on"
          >
            <v-icon @click="clickedTrash">mdi-delete-circle-outline</v-icon>
          </v-btn>
        </template>
        <span>Trash {{ formattedName }}</span>
      </v-tooltip>
    </allowed>
    <allowed :code="`${ pluralName }.restore`">
      <v-tooltip bottom v-if="restore">
        <template v-slot:activator="{ on, attrs }">
          <v-btn 
            icon
            v-bind="attrs"
            v-on="on"
          >
            <v-icon @click="clickedRestore">mdi-restore</v-icon>
          </v-btn>
        </template>
        <span>Restore {{ formattedName }}</span>
      </v-tooltip>
    </allowed>
    <allowed :code="`${ pluralName }.delete`">
      <v-tooltip bottom v-if="deletePermanently">
        <template v-slot:activator="{ on, attrs }">
          <v-btn 
            icon
            v-bind="attrs"
            v-on="on"
          >
            <v-icon @click="clickedDelete">mdi-delete-circle-outline</v-icon>
          </v-btn>
        </template>
        <span>Delete Permanently {{ formattedName }}</span>
      </v-tooltip>
    </allowed>
    <slot name="append"></slot>
  </div>
</template>

<script>
import Resource from '@/core/models/Resource'
import { mapActions } from 'vuex'
import Allowed from '../Allowed/Allowed.vue'
import pluralize from 'pluralize'

export default {
  components: { Allowed },
  props: [ 'api', 'view', 'edit', 'trash', 'restore', 'deletePermanently', 'name', 'item', 'modifyOnClickEdit' ],

  data: () => ({
    resource: new Resource
  }),

  computed: {
    formattedName () {
      return this.name.charAt(0).toUpperCase() + this.name.slice(1)
    },
    
    pluralName () {
      return pluralize( this.name )
    }
  },

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
      setDialog: 'dialog/set',
      hideDialog: 'dialog/hide',
      setAuth: 'auth/set',
    }),

    clickedView () {
      this.$router.push( 
        this.formatTo( this, 
        `${ this.pluralName }.show`, 
        { id: this.item._id } ) 
      )
    },

    clickedEdit () {
      if( this.modifyOnClickEdit ) return this.$emit( 'onClickEdit', this.item )
      this.$router.push( 
        this.formatTo( this, 
        `${ this.pluralName }.edit`, 
        { id: this.item._id } ) 
      )
    },

    async clickedTrash () {
      this.setDialog({
        model: true,
        title: 'Trash an item',
        text: `Are you sure to trash "${ this.item.name || this.name }"?`,
        confirm: true,
        proceed: async () => {
          this.setDialog({ loading: true })
          const data = await this.resource.request({
            method: 'patch',
            url: this.api.trash( this.item._id )
          })

          if( !data ) return

          this.hideDialog()
          this.setSnackbar({
            model: true,
            text: `${this.formattedName} trashed`,
            timeout: 3000
          })

          this.$emit( 'changed' )
        }
      })
    },

    async clickedRestore () {
      this.setDialog({
        model: true,
        title: 'Restore an item',
        text: `Are you sure to restore "${ this.item.name || this.name }"?`,
        confirm: true,
        proceed: async () => {
          this.setDialog({ loading: true })
          const data = await this.resource.request({
            method: 'patch',
            url: this.api.restore( this.item._id )
          })

          if( !data ) return

          this.hideDialog()
          this.setSnackbar({
            model: true,
            text: `${this.formattedName} restored`,
            timeout: 3000
          })

          this.$emit( 'changed' )
        }
      })
    },

    async clickedDelete () {
      this.setDialog({
        model: true,
        title: 'Delete an item',
        text: `Are you sure to delete "${ this.item.name || this.name }" permanently?`,
        confirm: true,
        proceed: async () => {
          this.setDialog({ loading: true })
          const data = await this.resource.request({
            method: 'delete',
            url: this.api.alone( this.item._id )
          })

          if( !data ) return

          this.hideDialog()
          this.setSnackbar({
            model: true,
            text: `${this.formattedName} deleted`,
            timeout: 3000
          })

          this.$emit( 'changed' )
        }
      })
    }
  },
}
</script>