<template>
  <v-badge
    color="transparent"
    offset-x="40"
    offset-y="40"
  >
    <template v-slot:badge>
      <v-btn icon class="primary" color="white" bordered @click="inputImage">
        <v-icon>mdi-circle-edit-outline</v-icon>
      </v-btn>
    </template>
    <v-avatar size="200" >
      <img
        :src="image"
        :alt="alt"
        ref="preview"
      >
    </v-avatar>
    <input 
      type="file" 
      :name="attrName"
      ref="input"
      style="visibility: hidden; width: 0; height: 0"
    >
  </v-badge>
</template>

<script>
import Api from '@/modules/Setting/routes/api.js'
import Resource from '@/core/models/Resource.js'
import { mapActions } from 'vuex'

export default {
  props: [ 'src', 'alt', 'name' ],

  computed: {
    image () {
      return this.src || '/storage/images/placeholder.jpeg'
    },

    attrName () {
      return this.name || 'image'
    }
  },

  data: () => ({
    resource: new Resource,
  }),

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
    }),

    inputImage () {
      this.$refs.input.click()
    },

    async submit () {
      const data = await this.setting.request({
        method: 'post',
        url: Api.editAll(),
        data: this.resource.readyData( this.$refs.form.$el ),
      })
      if( data ) {
        this.setSnackbar({
          model: true,
          text: 'Image updated',
          timeout: 3000
        })
        this.$emit( 'updated' )
      }
    },
  },

  mounted () {
    this.$refs.input.onchange = ( e ) => {
      this.$refs.preview.src = URL.createObjectURL( e.target.files[0] )
      this.$emit( 'input' )
    }
  }
}
</script>