import Auth from '@/core/models/Auth'

let routes = []

const requireRoute = require.context(
  // The relative path of the components folder
  '@/modules/',
  // Whether or not to look in subfolders
  true,
  // The regular expression used to match base component filenames
  /routes\/main\.js$/
)

requireRoute.keys().forEach(fileName => {
  const routeConfig = requireRoute(fileName)
  routes = [ ...routes, ...routeConfig.default ]
})

export default {
  path: '/main',
  name: 'main',
  redirect: { name: 'dashboard' },
  children: routes,
  component: () => import('@/components/Layouts/AppLayout.vue'),
  beforeEnter: ( to, from, next ) => {
    const auth = new Auth
    if( !auth.isAuthenticated() ) next({ name: 'public' })
    const permissions = auth.user.roleDetails.permissions
    if( to.meta.permission && !permissions.find( permission => permission.code === to.meta.permission ) ) next( { name: 'dashboard' })
    window.axios.defaults.headers.common['Authorization'] = `Bearer ${auth.token}`
    next()
  }
}