<template>
  <v-card elevation="0">
    <p>Property</p>
    <v-treeview
      v-if="!resources.isFetching"
      :items="resources.data"
      v-model="dataset"
      item-key="_id"
      multiple-active
      selectable
      dense
    >
    </v-treeview>
    <div class="v-text-field__details pl-3">
      <div class="v-messages error--text" role="alert">
        <div class="v-messages__wrapper">
          <div class="v-messages__message message-transition-enter-to" v-for="(error,i) in errors" :key="i" v-text="error">"description" is required</div>
        </div>
      </div>
    </div>
    <input v-for="(item, i) in dataset" :key="i" type="hidden" name="properties[]" :value="item">
  </v-card>
</template>

<script>
import Resources from '@/core/models/Resources'

export default {
  props: [ 'requestOptions', 'value', 'errors' ],

  computed: {
    dataset: {
      get () {
        return this.value
      },
      set ( val ) {
        this.$emit( 'input', this.process( val ) )
      }
    }
  },

  data: () =>({
    resources: new Resources
  }),

  methods: {
    process ( value ) {
      if( value.length - this.value.length > 1 )
        value = [ ...this.value ]
      else {
        const added = value.find( item => !this.value.includes( item ) )
        for(const item of this.resources.data ) {
          if( !item.children.find( item => item._id === added ) ) continue
          value = value.filter( item2 => !item.children.find( item3 => item3._id === item2 ) || item2 === added )
          break
        }
      }
      return value
    }
  },

  mounted () {
    this.resources.getResources( this.requestOptions )
  }
}
</script>