const state = () => ({
  dialog: {
    model: false,
    width: 500,
    title: 'Please enter a title to dialog.',
    text: 'Please enter a text to dialog.',
    confirm: false,
    proceed: () => alert( 'Please add action to proceed property' ),
    loading: false
  },
})

const getters = {
  dialog: state => state.dialog,
}

const mutations = {
  'SET' ( state, payload ) {
    state.dialog = { ...state.dialog, ...payload }
  },

  'SHOW' (state) {
    state.dialog.model = true
    state.dialog.loading = false
  },

  'HIDE' (state) {
    state.dialog.model = false
    state.dialog.loading = false
  },
}

const actions = {
  set: ({commit}, payload) => {
    commit('SET', payload)
  },

  show: ({commit}) => {
    commit('SHOW')
  },

  hide: ({commit}) => {
    commit('HIDE')
  },
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}
