<template>
  <v-card elevation="0">
    <v-row class="px-5 py-2">
      <v-col cols="12" lg="4" class="d-flex align-center">
        <v-text-field
          hide-details
          label="Search"
          append-icon="mdi-magnify"
          single-line
          v-model="resources.search"
          @input="searching"
          color="black"
          outlined
          dense
        ></v-text-field>
      </v-col>
      <v-col cols="12" lg="8" class="text-right">
        <allowed :code="`${ pluralName }.create`" v-if="add">
          <v-btn 
            :to="{ name: `${ pluralName }.create` }"
            text
          >
            <span v-text="`Add`"></span>
          </v-btn>
        </allowed>
        <allowed :code="`${ pluralName }.trash`" v-if="trash">
          <v-btn 
            text
            @click="trashAll"
          >
            <span v-text="`Trash`"></span>
          </v-btn>
        </allowed>
        <allowed :code="`${ pluralName }.restore`" v-if="restore">
          <v-btn 
            text
            @click="restoreAll"
          >
            <span v-text="`Restore`"></span>
          </v-btn>
        </allowed>
        <allowed :code="`${ pluralName }.delete`" v-if="deletePermanently">
          <v-btn 
            text
            @click="deleteAll"
          >
            <span v-text="`Delete`"></span>
          </v-btn>
        </allowed>
      </v-col>
    </v-row>

    <v-data-table
      :headers="headers"
      :items="resources.data"
      :loading="resources.isFetching"
      :options.sync="resources.options"
      :pageCount="resources.options.pageCount"
      :server-items-length="resources.options.itemsLength"
      @update:options="optionsChanged"
      loading-text="Please wait. Loading data..."
      show-select
      v-model="selected"
      item-key="_id"
      :single-expand="singleExpand"
      :expanded.sync="expanded"
      :show-expand="showExpand"
    >
      <template v-slot:expanded-item="{ headers, item }">
        <slot name="expanded-item" v-bind="{ headers, item }"></slot>
      </template>
      <template v-slot:item.image="{ item }">
        <v-avatar size="40">
          <img :src="item.image" />
        </v-avatar>
      </template>
      <template v-slot:item.actions="{ item }">
        <slot name="item.actions" v-bind="{ item }">
          <option-buttons
            :api="api"
            :deletePermanently="deletePermanently"
            :edit="edit"
            :item="item"
            :name="name"
            :restore="restore"
            :trash="trash"
            :view="view"
            @changed="optionsChanged"
          ></option-buttons>
        </slot>
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
import { mapActions } from 'vuex'
import pluralize from 'pluralize'

export default {
  props: [ 
    'resources', 
    'api', 
    'optionsChanged', 
    'headers', 
    'name', 
    'textproperty',
    'add',
    'deletePermanently',
    'edit',
    'restore',
    'trash',
    'view',
    'singleExpand',
    'expanded',
    'showExpand'
  ],

  computed: {
    pluralName () {
      return pluralize( this.name )
    }
  },

  data: () => ({
    selected: []
  }),

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
      setDialog: 'dialog/set',
      hideDialog: 'dialog/hide',
    }),

    searching () {
      this.resources.options = {
        ...this.resources.options,
        page: 1,
        pageCount: 0,
        pageStart: 0,
      }
      this.optionsChanged()
    },

    isNoSelectedItems () {
      if( this.selected.length === 0) {
        this.setDialog({
          model: true,
          title: 'No selected items',
          text: `Please select items to proceed.`
        })

        return true
      }
      
      return false
    },

    async trashAll () {
      if( this.isNoSelectedItems() ) return
      let data = null
      this.setDialog({
        model: true,
        title: 'Trash an item',
        text: `Are you sure to trash ${ this.selected.length} selected items?`,
        confirm: true,
        proceed: async () => {
          this.setDialog({ loading: true })
          for( const item of this.selected ) {
            data = await this.resources.request({
              method: 'patch',
              url: this.api.trash( item._id )
            })
          }

          if( !data ) return

          this.hideDialog()
          this.setSnackbar({
            model: true,
            text: `Selected items trashed`,
            timeout: 3000
          })

          this.optionsChanged()
        }
      })
    },

    async restoreAll () {
      if( this.isNoSelectedItems() ) return
      let data = null
      this.setDialog({
        model: true,
        title: 'Restore an item',
        text: `Are you sure to restore ${ this.selected.length} selected items?`,
        confirm: true,
        proceed: async () => {
          this.setDialog({ loading: true })
          for( const item of this.selected ) {
            data = await this.resources.request({
              method: 'patch',
              url: this.api.restore( item._id )
            })
          }

          if( !data ) return 

          this.hideDialog()
          this.setSnackbar({
            model: true,
            text: `Selected items restored`,
            timeout: 3000
          })

          this.optionsChanged()
        }
      })
    },

    async deleteAll () {
      if( this.isNoSelectedItems() ) return
      let data = null
      this.setDialog({
        model: true,
        title: 'Delete an item',
        text: `Are you sure to delete ${ this.selected.length} selected items permanently?`,
        confirm: true,
        proceed: async () => {
          this.setDialog({ loading: true })
          for( const item of this.selected ) {
            data = await this.resources.request({
              method: 'delete',
              url: this.api.alone( item._id )
            })
          }

          if( data !== '' ) return

          this.hideDialog()
          this.setSnackbar({
            model: true,
            text: `Selected items deleted`,
            timeout: 3000
          })

          this.optionsChanged()
        }
      })
    },
  }
}
</script>