<template>
  <v-snackbar
    v-model="snackbarmodel"
    :timeout="snackbar.timeout"
    top
  >
    <slot>
      <span v-text="snackbar.text"></span>
    </slot>

    <template v-slot:action="{ attrs }">
      <v-btn
        color="blue"
        text
        v-bind="attrs"
        @click="snackbarmodel = false"
      >
        Close
      </v-btn>
    </template>
  </v-snackbar>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  computed: {
    ...mapGetters({
      snackbar: 'snackbar/snackbar',
    }),

    snackbarmodel: {
      get () {
        return this.snackbar.model
      },

      set ( val ) {
        this.setSnackbar({ model: val })
      }
    }
  },

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
    }),
  }
}
</script>