<template>
  <validation-observer ref="validation">
    <input type="hidden" ref="id" name="id" :value="dataset._id" />
    <v-form ref="form" @submit.prevent="submit" enctype="multipart/form-data" :disabled="category.isFetching">
      <v-card elevation="0">
        <v-card-title v-text="`Category`"></v-card-title>
        <v-card-text>
          <form-errors :error="category.errors.message"></form-errors>
          <validation-provider vid="name" v-slot="{ errors }">
            <v-text-field
              :error-messages="errors"
              label="Name"
              name="name"
              outlined
              v-model="dataset.name"
              @input="category.prestine( false )"
            ></v-text-field>
          </validation-provider>
          <validation-provider vid="parent" v-slot="{ errors }">
            <parent-category-selector 
              v-if="!category.isFetching" 
              :errors="errors"
              v-model="dataset.parent" 
              :type="type"
            ></parent-category-selector>
          </validation-provider>
          <input type="hidden" name="type" :value="type">
        </v-card-text>
        <v-card-actions> 
          <v-btn color="primary" type="submit" :loading="category.isFetching" :disabled="category.isPrestine">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-form>
  </validation-observer>
</template>

<script>
import Category from '@/core/models/Category.js'
import { mapActions } from 'vuex'

export default {
  props: [ 'type', 'value', 'data' ],

  computed: {
    dataset: {
      get () { return this.value },
      set ( value ) { this.$emit( 'input', value ) }
    }
  },
  
  data: () =>({
    category: new Category
  }),

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
    }),

    async submit () {
      let 
        method = 'post',
        url = '/api/v1/categories',
        message = 'Category created'

      if( this.dataset._id ) {
        method = 'patch'
        url = `/api/v1/categories/${ this.dataset._id }`
        message = 'Category updated'
      }

      const data = await this.category.request({
        method,
        url,
        data: this.category.readyData( this.$refs.form.$el ),
      })
      
      if( !data ) 
        return this.$refs.validation.setErrors( this.category.errors.details )
        
      this.setSnackbar({
        model: true,
        text: message,
        timeout: 3000
      })

      this.dataset = {}

      this.$emit( 'submitted' )
    },
  },

  mounted () {
    this.category.fetch( false )
    this.category.load( false )
  }
}
</script>