<template>
  <v-dialog
    v-model="dialogmodel"
    :max-width="dialog.width"
  >
    <v-card>
      <v-card-title class="headline" v-text="dialog.title"></v-card-title>
      <v-card-text v-text="dialog.text"></v-card-text>
      <v-card-actions>
        <slot name="actions">
          <template v-if="dialog.confirm">
            <v-btn
              text
              @click="dialog.proceed"
              :loading="dialog.loading"
            >
              Agree
            </v-btn>
            <v-btn
              text
              @click="dialogmodel = false"
            >
              Disagree
            </v-btn>
          </template>
          <template v-else>
            <v-btn
              text
              @click="dialogmodel = false"
            >
              Okay
            </v-btn>
          </template>
        </slot>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  computed: {
    ...mapGetters({
      dialog: 'dialog/dialog',
    }),

    dialogmodel: {
      get () {
        return this.dialog.model
      },

      set ( val ) {
        this.setDialog({ model: val })
      }
    }
  },

  methods: {
    ...mapActions({
      setDialog: 'dialog/set',
    }),
  }
}
</script>