import Resource from './Resource'

export default class extends Resource {
  constructor () {
    super()
    this.headers = []
    this.data = []
    this.search = ""
    this.options = {
      itemsLength: 0,
      itemsPerPage: 10,
      page: 1,
      pageCount: 0,
      pageStart: 0,
      pageStop: 9,
    }
    this.fetchCount = 0
  }

  updateData ( response ) {
    const { 
      list, 
      itemsLength, 
      page, 
      itemsPerPage, 
      pageCount,
      pageStart,
      pageStop
    } = response

    this.data = list

    this.options = {
      ...this.options,
      itemsLength,
      page,
      itemsPerPage,
      pageCount,
      pageStart,
      pageStop,
    } 
  }

  async getResources ( options = null) {
    const data = await this.request( options )
    this.updateData( data )
  }

  optionsChanged ( options ) {
    if( this.isFetching ) return
    this.getResources( options )
  }
}