const state = () => ({
  snackbar: {
    model: false,
    text: '',
    timeout: -1
  },
})

const getters = {
  snackbar: state => state.snackbar,
}

const mutations = {
  'SET' ( state, payload ) {
    state.snackbar = { ...state.snackbar, ...payload }
  },

  'SHOW' (state) {
    state.snackbar.model = true
  },

  'HIDE' (state) {
    state.snackbar.model = false
  },
}

const actions = {
  set: ({commit}, payload) => {
    commit('SET', payload)
  },

  show: ({commit}) => {
    commit('SHOW')
  },

  hide: ({commit}) => {
    commit('HIDE')
  },
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}
