<template>
  <v-form ref="form" @submit.prevent="submit" enctype="multipart/form-data" :disabled="resource.isFetching">
    <v-card elevation="0">
      <v-card-title v-text="title"></v-card-title>
      <v-card-text class="text-center">
        <form-errors :errors="resource.errors"></form-errors>
        <image-selector :src="src" :alt="title" @input="resource.prestine( false )"></image-selector>
      </v-card-text>
      <v-card-actions>
        <v-btn color="primary" type="submit" :loading="resource.isFetching" :disabled="resource.isPrestine">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-form>
</template>

<script>
import Resource from '@/core/models/Resource.js'
import { mapActions } from 'vuex'

export default {
  props: [ 'title', 'src', 'url' ],

  data: () => ({
    resource: new Resource
  }),

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
    }),

    async submit () {
      this.$emit( 'submitted' )
      const data = await this.resource.request({
        method: 'post',
        url: this.url,
        data: this.resource.readyData( this.$refs.form.$el ),
      })
      if( data ) {
        this.setSnackbar({
          model: true,
          text: 'Image updated',
          timeout: 3000
        })

        this.resource.prestine()

        this.$emit( 'updated' )
      } else
        this.$emit( 'failed' )
    },
  },

  mounted () {
    this.resource.fetch( false )
  },
}
</script>