<template>
  <v-navigation-drawer
    v-model="sidebarmodel"
    app
    class="secondary"
  >
    <v-list-item>
      <v-list-item-avatar tile>
        <logo />
      </v-list-item-avatar>

      <v-list-item-content>
        <v-list-item-title><router-link to="/" exact class="d-flex align-center text-decoration-none white--text"><app-name></app-name></router-link></v-list-item-title>
      </v-list-item-content>
    </v-list-item>

    <v-divider></v-divider>

    <v-list nav dark>
      <template v-for="(item,i) in items">

        <v-list-group
          :prepend-icon="item.icon"
          v-if="item.childrenData && allowed( item.code )" 
          no-action
          :value="active( item )"
          :key="i"
          color="white"
        >
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title v-text="item.title"></v-list-item-title>
            </v-list-item-content>
          </template>

          <template
            v-for="(cItem, i) in item.childrenData"
          >
            <v-list-item
              :key="i"
              link 
              exact 
              :to="{ name: cItem.name }" 
              :value="active( item )"
              v-if="allowed( cItem.code )"
              color="white"
            >
              <v-list-item-title v-text="cItem.title"></v-list-item-title>

              <v-list-item-icon>
                <v-icon></v-icon>
              </v-list-item-icon>
            </v-list-item>
          </template>
        </v-list-group>
        <v-list-item 
          v-else-if="allowed( item.code )"
          link 
          exact 
          :to="{ name: item.name }" 
          :key="i"
          color="white"
        >
          <v-list-item-icon>
            <v-icon v-text="item.icon"></v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title v-text="item.title"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </template>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import items from './config/sidebar'
import includes from 'lodash/includes'
import { mapGetters, mapActions } from 'vuex'

export default {
  computed: {
    ...mapGetters({
      sidebar: 'sidebar/sidebar',
      auth: 'auth/auth',
    }),

    sidebarmodel: {
      set ( value ) {
        this.toggle( value )
      },
      get () {
        return this.sidebar.model
      },
    }
  },

  data: () => ({
    items: items,
    selectedItem: null,
    permissions: JSON.parse( localStorage.getItem( 'user' ) ).roleDetails.permissions
  }),

  methods: {
    ...mapActions({
      toggle: 'sidebar/toggle',
    }),

    active ( path ) {
      return includes( path.children, this.$route.name )
    },

    allowed ( code ) {
      return !code || this.permissions.find( permission => permission.code === code )
    },
  }
}
</script>