<template>
  <v-select
    :error-messages="errors"
    :items="getList"
    item-text="name"
    item-value="_id"
    label="Parent Category"
    name="parent"
    outlined
    v-model="dataset"
  ></v-select>
</template>

<script>
import Resources from '@/core/models/Resources'

export default {
  props: [ 'value', 'errors' ],

  computed: {
    dataset: {
      get () { return this.value || '' },
      set ( val ) { this.$emit( 'input', val ) }
    },

    getList () {
      return [ { _id: '', name: 'None' }, ...this.resources.data ]
    }
  },

  data: () => ({
    resources: new Resources
  }),

  mounted () {
    this.resources.getResources({
      method: 'GET',
      url: '/api/v1/categories',
      params: {
        per_page: -1,
        q: {
          type: 'product'
        }
      }
    })
  }
}
</script>